.stages-transformation {
    &__wrap {
        display: flex;
        flex-direction: column;
        gap: rem(54);

        @include mobile {
            gap: rem(20);
        }
    }

    &__blocks {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, rem(112));
        // grid-auto-rows: minmax(rem(112), auto);
        gap: rem(20);

        @include tablet {
            grid-template-rows: initial;
            grid-auto-rows: minmax(rem(112), auto);
        }

        @include mobile {
            display: none;
        }
    }

    &__block {
        counter-increment: item-counter;

        &:nth-child(3) {
            grid-row: span 2;

            @include tablet {
                grid-row: initial;
            }
        }

        &:nth-child(6) {
            grid-row: span 2;

            @include tablet {
                grid-row: initial;
            }
        }

        &:nth-child(7) {
            grid-column: span 2;
            grid-row: span 2;

            @include tablet {
                grid-column: span 3;
                grid-row: initial;
            }

            .stages-item {
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    top: rem(-97);
                    right: rem(-64);
                    z-index: 1;
                    background-image: url("../img/stages-item-background-airplane-image.png");
                    background-image: image-set(url("../img/stages-item-background-airplane-image.avif") type("image/avif"),
                            url("../img/stages-item-background-airplane-image.webp") type("image/webp"),
                            url("../img/stages-item-background-airplane-image.png") type("image/png"));
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                    width: rem(400);
                    height: rem(234);
                }

                &__text {
                    max-width: rem(446);
                    width: 100%;
                }
            }
        }

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(4),
        &:nth-child(5) {
            overflow: hidden;

            .stages-item {
                background-image: url("../img/stages-item-background-image-1.jpg");
                background-image: image-set(url("../img/stages-item-background-image-1.avif") type("image/avif"),
                        url("../img/stages-item-background-image-1.webp") type("image/webp"),
                        url("../img/stages-item-background-image-1.jpg") type("image/jpeg"));
            }
        }

        &:nth-child(3) {
            .stages-item {
                background-image: url("../img/stages-item-background-image-2.jpg");
                background-image: image-set(url("../img/stages-item-background-image-2.avif") type("image/avif"),
                        url("../img/stages-item-background-image-2.webp") type("image/webp"),
                        url("../img/stages-item-background-image-2.jpg") type("image/jpeg"));
            }
        }

        &:nth-child(6),
        &:nth-child(7) {

            .stages-item {
                background-image: url("../img/stages-item-background-image-3.jpg");
                background-image: image-set(url("../img/stages-item-background-image-3.avif") type("image/avif"),
                        url("../img/stages-item-background-image-3.webp") type("image/webp"),
                        url("../img/stages-item-background-image-3.jpg") type("image/jpeg"));
            }
        }
    }
}