.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-family: var(--font-family-golos-text);
    text-align: center;
    user-select: none;
    transition: all var(--transition-1);

    @include mobile {
        br {
            display: none;
        }
    }

    &--sm {
        line-height: 120%;
        font-weight: 500;
        color: var(--color-indigo-blue);
        padding: rem(7) rem(12);
        border-radius: rem(40);

        &.button--border {
            border: rem(1) solid var(--color-indigo-blue);
            background-color: transparent;

            @include min-pc {

                &:hover,
                &:focus-visible {
                    color: var(--color-white);
                    background-color: var(--color-indigo-blue);
                }
            }

            @include tablet {
                @media (hover: hover) {
                    &:hover {
                        color: var(--color-white);
                        background-color: var(--color-indigo-blue);
                    }
                }

                @media (hover: none) {

                    &:active,
                    &:focus-visible {
                        color: var(--color-white);
                        background-color: var(--color-indigo-blue);
                    }
                }
            }
        }
    }

    &--xl {
        font-size: rem(16);
        line-height: 130%;
        font-weight: 500;
        padding: rem(13) rem(20);
        border-radius: rem(66);
        max-width: rem(263);
        width: 100%;
        min-height: rem(72);

        @include mobile {
            max-width: 100%;
            padding: rem(13) rem(20);
            min-height: rem(47);
        }
    }

    &--black {
        color: var(--color-white);
        background-color: var(--color-deep-black);

        @include min-pc {

            &:hover,
            &:focus-visible {
                color: var(--color-deep-black);
                background-color: var(--color-golden-yellow);
            }
        }

        @include tablet {
            @media (hover: hover) {
                &:hover {
                    color: var(--color-deep-black);
                    background-color: var(--color-golden-yellow);
                }
            }

            @media (hover: none) {

                &:active,
                &:focus-visible {
                    color: var(--color-deep-black);
                    background-color: var(--color-golden-yellow);
                }
            }
        }
    }

    &--border {
        border: rem(2) solid var(--color-deep-black);
        background: transparent;

        @include min-pc {

            &:hover,
            &:focus-visible {
                color: var(--color-white);
                background-color: var(--color-deep-black);
            }
        }

        @include tablet {
            @media (hover: hover) {
                &:hover {
                    color: var(--color-white);
                    background-color: var(--color-deep-black);
                }
            }

            @media (hover: none) {

                &:active,
                &:focus-visible {
                    color: var(--color-white);
                    background-color: var(--color-deep-black);
                }
            }
        }
    }
}