.stages-slider {
    display: none;
    position: relative;
    padding-bottom: rem(64);

    @include mobile {
        display: block;
    }

    .swiper {
        position: static;
    }

    .swiper-wrapper {
        counter-reset: item-counter;
    }

    .swiper-slide:first-child {
        .stages-item-mobile {
            position: relative;

            &::before {
                content: "";
                position: absolute;
                top: rem(-125);
                right: rem(-13);
                rotate: -4deg;
                z-index: 1;
                background-image: url("../img/stages-item-background-airplane-image.png");
                background-image: image-set(url("../img/stages-item-background-airplane-image.avif") type("image/avif"),
                        url("../img/stages-item-background-airplane-image.webp") type("image/webp"),
                        url("../img/stages-item-background-airplane-image.png") type("image/png"));
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center center;
                width: rem(335);
                height: rem(182);
            }
        }
    }
}

.stages-slider-buttons {
    display: inline-flex;
    align-items: center;
    gap: rem(16);
    position: absolute;
    top: initial;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    z-index: 1;

    .swiper-button-disabled {
        background-color: transparentize($color: #313131, $amount: 0.8);
    }
}

.stages-slider-pagination {
    display: inline-flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: rem(6);
    min-width: rem(74);
    width: initial;

    .swiper-pagination-bullet {
        width: rem(10);
        height: rem(10);
        background-color: var(--color-soft-grey);
        opacity: 1;
        margin: initial !important;

        &-active {
            background: var(--color-deep-black);
        }
    }
}

.stages-slider-button {
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: rem(44);
    height: rem(44);
    background-color: var(--color-dark-grey);
    border-radius: 50%;
    color: var(--color-white);
    cursor: pointer;
    transition: all var(--transition-1);

    svg {
        width: rem(17);
        height: rem(17);
    }

    &--prev {
        rotate: 180deg;
    }

    @include tablet {
        @media (hover: hover) {
            &:not(.swiper-button-disabled):hover {
                background-color: var(--color-golden-yellow);
            }
        }

        @media (hover: none) {

            &:not(.swiper-button-disabled):active,
            &:not(.swiper-button-disabled):focus-visible {
                background-color: var(--color-golden-yellow);
            }
        }
    }
}