html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  // scrollbar-gutter: stable;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: var(--font-family-merriweather);
  font-size: rem(16);
  font-weight: 400;
  color: var(--color-dark-grey);
  background: var(--maxuiux-color-gray-100);
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100dvh;
}

img,
video {
  display: flex;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--color-black);
}

button,
input,
label {
  font-family: var(--font-family-montserrat);
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
}

button {
  border: none;
  background-color: initial;
  cursor: pointer;
}

svg {
  display: flex;
}


img {
  user-select: none;
}