.stages-item-mobile {
    margin-top: rem(128);
    min-height: rem(204);
    padding: rem(62) rem(20) rem(34);
    background-color: var(--color-light-beige);
    background-image: url("../img/stages-item-background-image-mobile.jpg");
    background-image: image-set(url("../img/stages-item-background-image-mobile.avif") type("image/avif"),
            url("../img/stages-item-background-image-mobile.webp") type("image/webp"),
            url("../img/stages-item-background-image-mobile.jpg") type("image/jpeg"));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    &__items {
        display: flex;
        flex-direction: column;
        gap: rem(28);
    }

    &__item {
        counter-increment: item-counter;
    }

    .stages-item__text {
        font-size: rem(18);
    }
}