@mixin min-pc {
    @media (min-width: $breakpoint-pc) {
        @content;
    }
}

@mixin pc {
    @media (max-width: $breakpoint-pc) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: $breakpoint-tablet) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: $breakpoint-mobile) {
        @content;
    }
}