.title {
    display: inline;
    font-size: rem(36);
    line-height: 135%;
    font-weight: 400;
    text-transform: uppercase;
    text-wrap: balance;

    @include mobile {
        font-size: rem(28);
        line-height: 138%;
    }

    br {
        @include mobile {
            display: none;
        }
    }

    &-color-red {
        font-weight: 700;
        color: var(--color-bright-red);
    }

    &--xl {
        font-size: rem(54);
        line-height: 120%;

        @include mobile {
            font-size: rem(36);
            line-height: 110%;
        }
    }
}


.link {
    display: inline-block;
    font-family: var(--font-family-golos-text);
    font-size: rem(20);
    line-height: 120%;
    font-weight: 400;
    color: var(--color-indigo-blue);

    br {
        @include mobile {
            display: none;
        }
    }

    &--title {
        vertical-align: center;
        margin-left: rem(6);
        max-width: rem(300);
        width: 100%;
        text-transform: initial;

        @include mobile {
            margin-left: initial;
            margin-top: rem(12);
        }
    }
}

.section-padding-xl {
    padding-top: rem(160);
    padding-bottom: rem(200);

    @include mobile {
        padding-top: rem(120);
        padding-bottom: rem(120);
    }
}

.section-padding-top-sm {
    padding-top: rem(80);

    @include mobile {
        padding-top: rem(48);
    }
}

.section-padding-bottom-md {
    padding-bottom: rem(138);

    @include mobile {
        padding-bottom: rem(100);
    }
}