.main-info {
    &__blocks {
        display: flex;
        flex-direction: column;
        gap: rem(65);
    }

    &__block {
        &--header {
            .main-info__items {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: rem(24);

                @include mobile {
                    align-items: initial;
                    flex-direction: column;
                    justify-content: initial;
                }
            }

            .main-info__item:nth-child(3) {
                display: none;

                @include mobile {
                    display: block;
                }
            }
        }

        &--body {
            .main-info__items {
                display: grid;
                grid-template-columns: rem(518) 1fr;

                @include mobile {
                    grid-template-columns: initial;
                    display: flex;
                    flex-direction: column;
                    gap: rem(24);
                }
            }
        }
    }

    &__item {
        &-header {
            margin-bottom: rem(45);
            
            @include mobile {
            margin-bottom: rem(35);
            }
        }

        &-body {
            margin-bottom: rem(33);
        }
    }

    &__image {
        &-ellipse {
            max-width: rem(394);
            max-height: rem(250);
            clip-path: ellipse(50% 50% at 50% 50%);
            
            @include mobile {
                max-width: 100%;
                max-height: 80%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
            }
        }

        &-cube {
            width: rem(527);
            transform: translateY(rem(-30)) translateX(rem(-25));

            @include mobile {
                max-width: 100%;
                width: 100%;
                transform: initial;
            }

            img {
                width: 100%;
            }
        }
    }
}