.items-slider {
    @include mobile {
        padding-bottom: rem(76);
    }

    .swiper {
        position: static;
    }

    &__slide {
        display: flex;
        flex-direction: column;
        align-items: center;

        &-image {
            width: rem(320);
            height: rem(320);
            border-radius: 50%;
            overflow: hidden;

            @include mobile {
                width: rem(244);
                height: rem(244);
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
            }
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            gap: rem(6);
            margin-top: rem(28);
            margin-bottom: rem(20);
        }

        &-title {
            font-size: rem(24);
            font-weight: 600;
        }

        &-text {
            font-size: rem(20);
            font-weight: 400;
            color: transparentize($color: #313131, $amount: 0.4);
        }

        &-title,
        &-text {
            font-family: var(--font-family-golos-text);
            line-height: 120%;
            text-align: center;
        }
    }
}

.items-slider-buttons {
    display: inline-flex;
    align-items: center;
    gap: rem(14);
    position: absolute;
    top: rem(10);
    right: 0;
    z-index: 1;

    @include mobile {
        top: initial;
        left: 50%;
        bottom: 0;
        right: initial;
        transform: translateX(-50%);
    }
}

.items-slider-button {
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: rem(44);
    height: rem(44);
    background-color: var(--color-dark-grey);
    border-radius: 50%;
    color: var(--color-white);
    cursor: pointer;
    transition: all var(--transition-1);

    svg {
        width: rem(17);
        height: rem(17);
    }

    &--prev {
        rotate: 180deg;
    }

    @include min-pc {

        &:hover,
        &:focus-visible {
            background-color: var(--color-golden-yellow);
        }
    }

    @include tablet {
        @media (hover: hover) {
            &:hover {
                background-color: var(--color-golden-yellow);
            }
        }

        @media (hover: none) {

            &:active,
            &:focus-visible {
                background-color: var(--color-golden-yellow);
            }
        }
    }
}

.items-slider-pagination {
    font-family: var(--font-family-golos-text);
    font-size: rem(16);
    font-weight: 400;
    line-height: 120%;
    width: initial;
    user-select: none;

    .swiper-pagination-slash,
    .swiper-pagination-total {
        color: transparentize($color: #313131, $amount: 0.4);
    }
}