.table {
    text-align: left;
    border-collapse: collapse;
    max-width: 100%;
    width: 100%;
    font-family: var(--font-family-golos-text);
    font-size: rem(20);
    line-height: 110%;
    position: relative;

    &::before,
    &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        z-index: 1;
        max-width: 100%;
        width: 100%;
        height: rem(16);
        background-color: var(--color-white);

        @include mobile {
            display: none;
        }
    }

    &::before {
        top: 0;
    }

    &::after {
        bottom: 0;
    }

    &-discount {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 57%;
            left: rem(-2);
            right: 0;
            transform: translateY(-50%);
            z-index: 1;
            max-width: 100%;
            width: 100%;
            height: rem(2);
            border-radius: rem(5);
            background-color: var(--color-bright-red);

            @include mobile {
                display: none;
            }
        }
    }

    tr {
        @include mobile {
            display: flex;
            flex-direction: column;
            gap: rem(12);
            padding-top: rem(12);
            padding-bottom: rem(16);
        }

        &:not(:last-child) {
            border-bottom: rem(2) solid var(--color-pastel-grey);
        }
    }

    th {
        width: 43.46%;
    }

    td {
        width: 56.53%;
    }

    th,
    td {
        border: rem(2) solid var(--color-pastel-grey);
        padding-top: rem(16);
        padding-bottom: rem(16);

        @include mobile {
            width: initial;
            border: none;
            padding-top: initial;
            padding-bottom: initial;
        }
    }

    tr:first-child th,
    tr:first-child td,
    tr:last-child td {
        border-top: none;
    }

    tr:last-child th,
    tr:last-child td {
        border-bottom: none;
    }

    tr th:first-child,
    tr td:first-child {
        font-weight: 400;
        border-left: none;
        padding-right: rem(16);
        color: transparentize($color: #313131, $amount: 0.3);

        @include mobile {
            padding-right: initial;
        }
    }

    tr th:last-child,
    tr td:last-child {
        border-right: none;
        padding-left: rem(16);
        font-weight: 600;

        @include mobile {
            padding-left: initial;
        }
    }
}