.stages-item {
    width: 100%;
    height: 100%;
    padding: rem(20);
    background-color: var(--color-light-beige);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    &__wrap {
        display: flex;
        gap: rem(16);
    }

    &__value {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: rem(2);
        font-weight: 600;
        width: rem(36);
        height: rem(36);
        border-radius: 50%;
        background-color: var(--color-white);
        overflow: hidden;
        user-select: none;

        &::before {
            content: counter(item-counter);
        }
    }

    &__text {
        font-weight: 500;
    }

    &__value,
    &__text {
        font-family: var(--font-family-golos-text);
        font-size: rem(20);
        line-height: 120%;
    }
}