.infinite-scroll {
    font-size: rem(22);
    line-height: 110%;
    font-weight: 400;
    text-transform: uppercase;
    color: var(--color-white);
    padding: rem(18) 0;
    background-color: var(--color-bright-red);
    user-select: none;
    pointer-events: none;
    overflow: hidden;

    @include mobile {
        font-size: rem(16);
        padding: rem(10) 0;
    }

    &-animation {
        &__text {
            display: inline-flex;
            text-wrap: nowrap;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            animation-direction: var(--direction);
            animation-duration: var(--duration);

            span {
                margin-right: rem(30);
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: rem(-19);
                    transform: translateY(-50%);
                    z-index: 1;
                    width: rem(6);
                    height: rem(6);
                    background-color: var(--color-white);
                    border-radius: 50%;
                }
            }
        }

        &--left {
            .infinite-scroll-animation__text {
                animation-name: infiniteScrollAnimationLeft;
            }

        }

        &--right {
            .infinite-scroll-animation__text {
                animation-name: infiniteScrollAnimationRight;
            }
        }
    }

    @keyframes infiniteScrollAnimationLeft {
        0% {
            transform: translateX(0);
        }

        100% {
            transform: translateX(-50%);
        }
    }

    @keyframes infiniteScrollAnimationRight {
        0% {
            transform: translateX(-50%);
        }

        100% {
            transform: translateX(0);
        }
    }
}