.header {
    padding: rem(26) 0;
    max-width: 100%;
    width: 100%;
    height: rem(700);
    background-color: var(--color-light-beige);
    background-image: url("../img/header-background-image-1.jpg");
    background-image: image-set(url("../img/header-background-image-1.avif") type("image/avif"),
            url("../img/header-background-image-1.webp") type("image/webp"),
            url("../img/header-background-image-1.jpg") type("image/jpeg"));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @include mobile {
        padding: rem(18) 0;
        background-image: url("../img/header-background-image-1-mobile.jpg");
        background-image: image-set(url("../img/header-background-image-1-mobile.avif") type("image/avif"),
                url("../img/header-background-image-1-mobile.webp") type("image/webp"),
                url("../img/header-background-image-1-mobile.jpg") type("image/jpeg"));
        height: rem(774);
    }

    &__logo {
        display: inline-block;
        color: var(--color-graphite-grey);

        svg {
            width: rem(245);
            height: rem(36);

            @include mobile {
                width: rem(213);
                height: rem(32);
            }
        }
    }

    &__blocks {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: rem(40);
        max-width: rem(542);
        width: 100%;
        padding-top: rem(62);

        @include mobile {
            gap: rem(32);
            padding-top: rem(40);
            margin: 0 auto;
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        gap: rem(24);
    }

    &__title {
        font-size: rem(60);
        font-weight: 400;
        line-height: 110%;
        text-transform: uppercase;

        span {
            display: block;

            &:nth-child(2) {
                text-indent: rem(91);

                @include mobile {
                    text-indent: rem(63);
                }
            }

            &:nth-child(3) {
                text-indent: rem(33);

                @include mobile {
                    text-indent: rem(30);
                }
            }
        }

        @include mobile {
            font-size: rem(36);
        }
    }

    &__subtitle {
        font-family: var(--font-family-golos-text);
        font-size: rem(18);
        line-height: 130%;
        font-weight: 400;
        text-align: center;
        text-wrap: balance;

        @include mobile {
            text-align: left;
        }
    }

    &__buttons {
        display: inline-flex;
        flex-wrap: wrap;
        align-items: center;
        gap: rem(14) rem(16);
        max-width: 100%;
        width: 100%;

        @include mobile {
            flex-direction: column;
        }

        .button--border {
            @include tablet {
                border: initial;
                background-color: transparentize($color: #fff, $amount: 0.1);

                @media (hover: hover) {
                    &:hover {
                        color: var(--color-white);
                        background-color: var(--color-deep-black);
                    }
                }

                @media (hover: none) {

                    &:active,
                    &:focus-visible {
                        color: var(--color-white);
                        background-color: var(--color-deep-black);
                    }
                }
            }
        }
    }
}